import React, { useState, useEffect, Component } from "react";
import axios from "axios";
const url = "https://api.imgflip.com/get_memes";

export default class Generator extends Component {
  render() {
    return (
      <div>
        <App />
      </div>
    );
  }
}
const App = () => {
  const [picture, setPicture] = useState("");
  const [dummy, setDummy] = useState("https://i.imgflip.com/28s2gu.jpg");
  const [topText, SetTopText] = useState();
  const [bottomText, setBottomText] = useState();

  useEffect(() => {
    axios.get(url).then((response) => setPicture(response.data.data.memes));
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    const randNum = Math.floor(Math.random() * picture.length);
    setDummy(picture[randNum].url);
  };

  if (!picture)
    return (
      <div className="container">
        <img
          className="invert"
          src={require("./loading-png-gif.gif")}
          alt="loading..."
        />
      </div>
    );

  return (
    <div>
      <form action="" className="meme-form" onSubmit={handleSubmit}>
        <button>Submit</button>
        <input
          type="text"
          name="topText"
          placeholder="Top Text"
          value={topText}
          onChange={(e) => SetTopText(e.target.value)}
        />
        <input
          type="text"
          name="bottomText"
          placeholder="Bottom Text"
          value={bottomText}
          onChange={(e) => setBottomText(e.target.value)}
        />
      </form>
      <div className="gamay">
        <div className="meme">
          <img src={dummy} alt="" />
          <h2 className="top">{topText}</h2>
          <h2 className="bottom">{bottomText}</h2>
        </div>
      </div>
    </div>
  );
};
